.no-gutters {
    margin: 0px;
}

.Header {
    padding: 2% 2% 0% 2%;
}
.HeaderFirstCommon {
    padding: 1%;    
}
.HeaderFirst *{
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.HeaderSearchBar .input-group.md-form.form-sm.form-2 input{
    border: 1px solid #bdbdbd;
    border-top-right-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
}
.HeaderNavigation a {
    padding: 0.5em 1em ;
    color: rgb(80, 76, 76);
    font-size: 16px;
    font-weight: 500;
}
.HeaderNavigation .NAvDropDown:hover {
    border-radius: 5px 5px 0px 0px;
    background-color: #97c9de !important;
}
 .NAvDropDown {
    /* position: relative; */
    /* display: inline-block; */
}
.NAvDropDown .NavDropDownContent h5 {
    color: black;
}
 .NavDropDownContent {
    display: none;
    position: absolute;
    top: 100%;
    width: auto;
    min-width: 95vw;
    left: 0vw;
    height: auto;
    background-color: #f1f1f1;
    /* min-width: 50vh; */
    min-height: 50vh;
    /* border-radius: 10px; */
    border: 1px solid lightgrey;
    -webkit-box-shadow: 0px 8px 10px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 10px rgba(0,0,0,0.2);
    z-index: 2;
    padding: 3%;
    /* clip: rect(auto, auto, auto, auto);  */
}
 .NAvDropDown:hover .NavDropDownContent{
     display: grid;
     grid-template-columns: 1fr 1fr 1fr; 
     /* color: blue; */
}

.NavDropDownContent ul li{
    padding: 0.5%;

}
.NavDropDownContent ul li:hover {
 background-color: lightblue;
}   
