a {
  text-decoration: none !important;
  color: rgb(80, 76, 76);
}
ul li {
  list-style: none;
}  
.App {
  font-size: 14px;
} 
#canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background: lightgrey;
  /* border: 1px solid grey; */
  border-radius: 5%;
}
#loadingImg{
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  /* background-color: lightblue; */
  position: absolute;
  top: 45%;
  left: 50%;
}


